}
.application-content .btn {
  padding: 0.6rem 2rem;
  cursor: pointer;
  color: #08768c;
  background: #fff;
  border: 2px solid #08768c;
  display: inline-block;
  text-decoration: none;
}
.application-content .btn-primary {
  text-transform: uppercase;
  background: #08768c;
  color: #fff;
}
.application-content .btn-primary:hover {
  color: #fff;
}
.application-content .btn-primary:focus {
  color: #fff;
}
.application-content .btn-secondary {
  text-transform: uppercase;
  font-weight: bold;
  background-color: transparent;
}
.application-content .btn-terciary {
  text-transform: uppercase;
  border: 0;
  font-weight: bold;
  background-color: transparent;
  padding: 0.6rem 0.5rem;
}
.application-content .btn-more {
  margin: 1.25rem 0;
  text-transform: uppercase;
  font-weight: bold;
}
.application-content .btn-hide-and-seek {
  background-color: transparent;
  transition: all 0.3s ease 0s;
  color: #fff;
  border-color: #fff;
}
.application-content .btn-hide-and-seek:hover {
  background-color: #fff;
  color: #1c1c1c;
}
.application-content .btn-hide-and-seek:focus {
  background-color: transparent;
  color: #fff;
}

.application-content .user-message {
  background-repeat: no-repeat;
  background-position: 28px 28px;
  background-size: 2em 2em;
  border-collapse: separate;
  color: #ffffff;
  padding: 2em;
  width: 100%;
}
.application-content .message-info {
  background-color: #89cffa;
}

.application-content .message-content {
  width: 100%;
  text-align: left;
}

.application-content .message-icon {
  width: 50px;
}

.application-content .user-message img,
.application-content .user-message p {
  margin: 0;
}
.application-content .user-message p {
  padding: 0 0 0 20px;
}

.application-content table.user-message td {
  vertical-align: top;
}

.loader-bg {
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(226, 226, 226, 0.5);
}

.loader {
  border: solid 15px transparent;
  border-bottom-color: #1d1d1d;
  border-radius: 50%;
  content: '';
  height: 10em;
  width: 10em;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}

/* Not a fan of this, but 'react-spinners' plugin doesn't let you change the inline style of inline-block or set a className to override */
.application-content > div:first-child {
  display: block !important;
}



